.nav {
  nav {
    > a {
      font-size: 40px;
      font-weight: 600;
      text-transform: uppercase;
      @media (max-width: 600px) {
        font-size: 30px;
      }
    }
  }
  .navItem {
    display: flex;
    align-items: center;
    button {
      border-radius: 14px;
      border-width: 2px;
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 700;
    }
    li {
      font-size: 14px;
      font-weight: 600;
      padding: 0 1.5rem;
      @media (max-width: 600px) {
        padding: 0.75rem 0;
      }
      a {
        padding: 0 !important;
      }
      img {
        width: 32px;
      }
    }
  }
}
