@import "../../custom.scss";

.application {
  > div {
    @media (max-width: 600px) {
      text-align: center;
    }
    h2 {
      font-size: 32px;
      font-weight: 700;
    }
    p {
      font-size: 14px;
      font-weight: 500;
      line-height: 30px;
      width: 70%;
      @media (max-width: 768px) {
        width: 100%;
      }
    }
    button {
      border-radius: 20px;
      font-weight: 700;
      padding: 1rem 4rem;
      margin-top: 1rem;
      @media (max-width: 600px) {
        width: 100%;
      }
    }
  }
}
