@import "../../custom.scss";

.Tabs {
  .formNav {
    @media (max-width: 600px) {
      padding-bottom: 3rem;
    }
  }
  .formArea {
    padding-top: 5rem;
    padding-bottom: 5rem;
    background-color: #f8f9fa;
    @media (max-width: 600px) {
      padding: 3rem 1rem;
    }
  }
  .form {
    box-shadow: 0px 10px 35px #041a2729;
    border-radius: 25px;
    background-color: #fff;
    @media (max-width: 600px) {
      padding: 3rem 1.5rem !important;
    }
    h2 {
      font-weight: 700;
    }
    button {
      border-radius: 20px;
      font-weight: 700;
      padding: 0.75rem 0;
      margin-top: 1rem;
    }
  }
}
